body {
    min-height: 500%;
}

.main-cont {
    display: flex;
    justify-content: center;
}

.cover-image-wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    transform: translateY(0px);
    opacity: 1;

    animation: image-out 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -3s);

    animation-iteration-count: 1;
    animation-fill-mode: both;

    .cover-image {
        width: 100%;
        height: 80%;
        background-image: url("../../assets/cover.jpg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        border-bottom-right-radius: 300px;
        border-bottom-left-radius: 300px;
    }
}

@keyframes image-out {
    to {
        transform: translateY(-400px);
        opacity: 0;
    }
}

.cover-name-wrapper {
    position: fixed;
    top: 5%;
    animation: name-out 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);

    animation-iteration-count: 1;
    animation-fill-mode: both;
    z-index: 1;

    .cover-name {
        width: 300px;
        height: 200px;
        background-image: url("../../assets/name.png");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
}

@keyframes name-out {
    75% {
        transform: translateY(100px);
        z-index: 1;
    }
    100% {
        transform: translateY(100px);
        z-index: 1;
    }
}

.cover-date-wrapper {
    position: fixed;
    bottom: -5%;

    transform: translateY(0px);
    opacity: 1;

    animation: date-out 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -3s);

    animation-iteration-count: 1;
    animation-fill-mode: both;

    .cover-date {
        width: 300px;
        height: 250px;
        background-image: url("../../assets/date.png");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
}

@keyframes date-out {
    to {
        transform: translateY(100px);
        opacity: 0;
    }
}

.content-image-wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    // transform: translateX(100px);
    opacity: 0;

    animation: image-in 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);

    animation-iteration-count: 1;
    animation-fill-mode: both;

    .content-image {
        width: 100%;
        height: 100%;
        object-fit: fill;
    }
}

@keyframes image-in {
    50% {
        opacity: 0.7;
    }
    100% {
        opacity: 1;
    }
}

.content-text-wrapper {
    position: fixed;
    padding: 10px 40px;
    bottom: 15%;
    transform: translateX(200px);
    opacity: 0;
    animation: text-in 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;

    .text-container {
        display: flex;
        flex-direction: column;
        text-align: end;
        gap: 10px;
        color: #3c515f;

        .desc,
        .address,
        .rsvp {
            font-family: "Montserrat-Regular";
        }

        .venue,
        .day {
            font-family: "Montserrat-Bold";
        }

        .date,
        .time,
        .contact {
            font-family: "Forum-Regular";
        }

        .day,
        .time {
            font-weight: bold;
        }

        .desc,
        .address,
        .contact,
        .time,
        .rsvp,
        .day {
            font-size: 12px;
        }

        .date {
            font-size: 25px;
        }

        .venue {
            font-size: 18px;
        }
    }
}

@keyframes text-in {
    25% {
        transform: translateX(100px);
        opacity: 0;
    }

    50% {
        transform: translateX(0px);
        opacity: 1;
    }
    100% {
        transform: translateX(200px);
        opacity: 0;
    }
}

.rsvp-form-wrapper {
    position: fixed;
    bottom: 5%;
    width: 70%;
    opacity: 0;
    animation: form-in 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;

    .ant-btn {
        background: #3c515f;
        color: #b1d1d6;

        &:disabled {
            background: lightgrey;
            color: white;
        }

        &:hover,
        &:focus {
            color: #b1d1d6 !important;
            border-color: #b1d1d6 !important;
        }
    }

    .ant-typography {
        font-family: "Montserrat-Regular";
    }
}
@keyframes form-in {
    90% {
        transform: translateY(-75px);
        opacity: 0;
    }
    100% {
        transform: translateY(-100px);
        opacity: 1;
    }
}

@media (min-width: 500px) {
    .cover-image-wrapper {
        width: 40%;
    }
    .content-image-wrapper {
        width: 40%;
    }
    .rsvp-form-wrapper {
        width: 25%;
    }
}

@media (min-width: 768px) {
    .cover-image-wrapper {
        width: 80%;
    }
    .content-image-wrapper {
        width: 80%;
    }
    .rsvp-form-wrapper {
        width: 40%;
    }
}

@media (min-width: 1200px) {
    .cover-image-wrapper {
        width: 40%;
    }
    .content-image-wrapper {
        width: 40%;
    }
    .rsvp-form-wrapper {
        width: 25%;
    }
}
